export default {
	data() {
		return {
			count:0
		}
	},
	methods: {
		project() {
			uni.navigateTo({
				url: '../../my_project/project/project',
			});
		},
		message() {
			uni.navigateTo({
				url: '../../messagepush/messagepush',
			});
		},
		setting(){
			uni.navigateTo({
				url: '../../setting/setting',
			});
		},

	},
	created() {
		const value = uni.getStorageSync('setUserData');
		if (value) {
			uni.request({
				url: this.apiServer + '/basicdata/components/message.php',
				data: {
					flag:'getMessage',
					peo:value.my_name
				},
				method: 'POST',
				dataType: 'json',
				header: {
					'content-type': 'application/x-www-form-urlencoded', //POST头信息
				},
				success: (res) => {
					res=res.data;
					console.log(JSON.stringify(res));
					if(res.length&&typeof(res)==Object){
						for(var i=0;i<res.length;i++){
							uni.sendSocketMessage({
								data: res[i]
							})
						}
					}
				},
				fail: (error) => {
					console.log(error);
				}
			});
		}
	},
	onLoad: function (option) {
		uni.request({
			url: this.api + '/basicdata/getmyclass.php',
			data: {
				flag:'classNum',
			},
			method: 'POST',
			dataType: 'json',
			header: {
				'content-type': 'application/x-www-form-urlencoded', //POST头信息
			},
			success: (res) => {
				console.log(res);
				res=res.data;
				this.count=res.count;
			},
			fail: (error) => {
				console.log(error);
			}
		});
	}
}
